import { request, requestDownload } from 'utils';

const prefix = '/api/students';
const SUMMITED = 1;
const UNSUMMITED = 1;

export const getClasss = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/Class',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const getClassById = id =>
  request({
    prefix,
    url: `/Class/${id}`,
    method: 'GET',
  });

export const deleteClass = id =>
  request({
    prefix,
    url: `/Class/${id}`,
    method: 'DELETE',
  });

export const updateClass = ({ id, ...values }) => {
  console.log('value ====>', values);
  return request({
    prefix,
    url: `/Class/${id}`,
    data: values,
    method: 'PUT',
  });
};

export const createClass = value =>
  request({
    prefix,
    url: `/Class`,
    data: value,
    method: 'POST',
  });

export const exportStudents = async (classId, className) =>
  requestDownload({
    defaultFileName: `Danh sách sinh viên - ${className.toUpperCase()}.pdf`,
    prefix,
    url: `/ClassEnrollment/${classId}/ExportReportClassStudent`,
    method: 'POST',
    data: {},
  });

export const exportStudentBackgrounds = async (classId, className) =>
  requestDownload({
    defaultFileName: `Danh sách lý lịch sinh viên - ${className.toUpperCase()}.pdf`,
    prefix,
    url: `/ClassEnrollment/${classId}/ExportReportBackgroundStudent`,
    method: 'POST',
    data: {},
  });

export const exportPaperMissing = async (classId, className) =>
  requestDownload({
    defaultFileName: `Danh sách sinh viên nộp thiếu - ${className.toUpperCase()}.pdf`,
    prefix,
    url: '/Student/ExportStatusPaperStudent',
    method: 'POST',
    data: { classId, paperStatus: UNSUMMITED },
  });

export const exportFullPaper = async (classId, className) =>
  requestDownload({
    defaultFileName: `Danh sách sinh viên nộp đủ - ${className.toUpperCase()}.pdf`,
    prefix,
    url: '/Student/ExportStatusPaperStudent',
    method: 'POST',
    data: { classId, paperStatus: SUMMITED },
  });



